/* Hero Section */
.hero-section {
  width: 100vw;
  margin-left: calc(-50vw + 50%);
  background: #181818;
  border-bottom: 0.5px solid #ffffff;
  padding: 30px 16px;
  text-align: left;
  margin: 0;
  margin-bottom: 0;
}

.hero-content {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0;
}

.hero-title {
  font-size: 28px;
  font-weight: bold;
  color: white;
  margin-bottom: 8px;
}

.hero-subtitle {
  font-size: 14px;
  font-weight: normal;
  color: white;
  margin-bottom: 20px;
}

.hero-buttons {
  display: flex;
  gap: 16px;
}

.hero-button {
  background: white;
  color: black;
  padding: 12px 24px;
  text-decoration: none;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  border-radius: 0;
  border: none;
  cursor: pointer;
  display: inline-block;
}

/* Tablet */
@media screen and (min-width: 768px) {
  .hero-section {
    padding: 40px 24px;
  }

  .hero-title {
    font-size: 36px;
  }
}

/* Desktop */
@media screen and (min-width: 1024px) {
  .hero-section {
    padding: 50px 32px;
  }

  .hero-title {
    font-size: 42px;
  }
}

/* Header Wrapper */
.header-top {
  width: 100%;
  background: #181818;
  border-top: 1px solid #fff;
  height: 40px;
  display: flex;
  align-items: center;
  padding: 0;
}

.header-top-content {
  width: 100%;
  padding: 10px 16px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 12px;
  color: #000;
  max-width: 1400px;
  margin: 0 auto;
  box-sizing: border-box;
}

.header-main {
  width: 100%;
  background: #181818;
  height: 64px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #fff;
  padding: 0;
}

.header-main-content {
  width: 100%;
  padding: 10px 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1400px;
  margin: 0 auto;
  box-sizing: border-box;
}

.header-nav {
  width: 100%;
  background: #181818;
  border-bottom: 1px solid #ececec;
  overflow-x: auto;
  padding: 0;
}

.header-nav-content {
  display: flex;
  width: 100%;
  padding: 0 16px;
  white-space: nowrap;
  max-width: 1400px;
  margin: 0 auto;
  box-sizing: border-box;
}

/* Tablet */
@media screen and (min-width: 768px) {
  .header-top-content,
  .header-main-content,
  .header-nav-content {
    padding-left: 24px;
    padding-right: 24px;
  }
}

/* Desktop */
@media screen and (min-width: 1024px) {
  .header-top-content,
  .header-main-content,
  .header-nav-content {
    padding-left: 32px;
    padding-right: 32px;
  }
}
